import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';

const formValidation = yup.object({
  name: yup.string().required('O campo  obrigatrio.'),
  email: yup.string().email('Preencha um email vlido').required('O campo  obrigatrio.'),
  subject: yup.string().required('O campo  obrigatrio.'),
  message: yup.string().required('O campo  obrigatrio.'),
});

export const formResolver = yupResolver(formValidation);